@import url(https://fonts.googleapis.com/css2?family=IM+Fell+DW+Pica+SC&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Special+Elite&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lora:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container-player {
  margin: 0.3em;
  -webkit-animation: moveInLeft 0.8s ease-out;
          animation: moveInLeft 0.8s ease-out;
}
.player-title {
  font-family: "Special Elite", cursive;
  display: inline-block;
  padding: 0.5em;
  font-size: 0.8em;
  text-align: center;
  border-radius: 5px;
  border: 2px solid white;
}

.icon {
  padding: 0.3em;
  max-height: 60px;
  object-fit: contain;
  object-position: center;
}

.player-icon {
  -webkit-filter: drop-shadow(5px 5px 5px #222222);
  filter: drop-shadow(5px 5px 5px #222222);
}
.icon-section {
  display: flex;
  justify-content: space-between;
}

@media (min-width: 750px) {
  .icon-section {
    flex-direction: column;
  }
  .icon {
    max-height: 120px;
  }
}

@-webkit-keyframes moveInLeft {
  0% {
    opacity: 0;
    transform: translateX(-5rem);
  }
  80% {
    transform: translateX(0.3rem);
  }
  100% {
    opacity: 1;
    transform: translate(0);
  }
}

@keyframes moveInLeft {
  0% {
    opacity: 0;
    transform: translateX(-5rem);
  }
  80% {
    transform: translateX(0.3rem);
  }
  100% {
    opacity: 1;
    transform: translate(0);
  }
}

.board-wrapper {
  height: 100%;
}
.container-game {
  display: flex;
  flex-direction: column;
}

.container-board-players {
  padding: 0 1em;
  display: flex;
  justify-content: space-between;
}

.board-buttons {
  display: flex;
  justify-content: center;
}

/* //Board// */
.board {
  border-top: 2px solid white;
  border-bottom: 2px solid white;
  border-radius: 5px;
  max-width: 90%;
  min-height: 270px;
  margin: 0 auto;
  display: flex;
  align-items: stretch;
  flex-wrap: wrap;
}

.row {
  position: relative;
  border-bottom: 2px solid white;
  width: 100%;
  display: flex;
}

.row:last-child {
  border-bottom: 0;
}

.cell {
  position: relative;
  background-color: rgba(43, 41, 41, 0.932);
  padding: 2em;
  width: 100%;
}

/* Player Background img */
.player {
  width: 100%;
  background-position: center; /* Center the image */
  background-repeat: no-repeat; /* Do not repeat the image */
  pointer-events: none;
  background-size: 2em;
}

.uncheck-cell {
  border-right: 2px solid white;
}

.uncheck-cell:first-child {
  border-left: 2px solid white;
}

.check-cell {
  border: 4px solid rgb(243, 165, 19);
}

.check-cell:first-child {
  border-left: 4px solid rgb(243, 165, 19);
}

footer {
  padding: 0.5em;
  position: fixed;
  bottom: 0;
  width: 100%;
  font-family: "Lora", serif;
  font-weight: 700;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Footer */
.footer-span {
  font-size: 0.8em;
  color: white;
}
.footer-link {
  font-size: 0.8em;
  text-decoration: none;
  color: white;
  margin-left: 1em;
}

.footer-link:focus,
.footer-link:hover {
  cursor: pointer;
  font-weight: 700;
}

@media (max-width: 749px) {
  .board {
    order: 99;
  }
  .container-board-players {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}

@media (min-width: 750px) {
  .container-board-players {
    padding-bottom: 9em;
  }
  .player {
    background-size: 3em;
  }
}

@media (min-width: 800px) {
  .container-board-players {
    padding-bottom: 0;
  }
  .board {
    min-width: 600px;
    min-height: 420px;
  }
}

.container-landing-page {
  display: flex;
  flex-direction: column;
}

.heroes-container {
  margin-bottom: 0.2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.hero {
  margin: 1em;
  padding: 1em;
  max-height: 150px;
  outline: 2px solid transparent;
  transition: all 0.5s ease-in-out;
  -webkit-animation: moveInBottom 0.8s ease-out;
          animation: moveInBottom 0.8s ease-out;
}

.game-buttons {
  display: flex;
  justify-content: center;
  margin-bottom: 0.2em;
  order: -1;
}

.game-title {
  margin-top: 2em;
  padding: 0.4em 0.2em;
  font-size: 2rem;
  text-align: center;
  -webkit-animation: moveInRight 0.8s ease-out;
          animation: moveInRight 0.8s ease-out;
}

.under-lined {
  text-decoration: underline;
}

.not-selected:hover,
.not-selected:focus {
  outline: 2px solid rgba(216, 239, 243, 0.781);
  transform: scale(1.05);
  opacity: 0.8;
  cursor: pointer;
}

.no-click,
.selected {
  pointer-events: none;
}

.selected {
  outline: 2px solid whitesmoke;
  transform: scale(1.05);
  opacity: 1;
}

@media (min-width: 400px) {
  .heroes-container {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
  }
}

@media (min-width: 500px) {
  .hero {
    max-height: 130px;
  }
  .heroes-container {
    margin-top: 3em;
  }
}

@media (min-width: 630px) {
  .heroes-container {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
  }
}

@media (min-width: 930px) {
  .heroes-container {
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: center;
    padding-top: 1em;
  }

  .game-buttons {
    order: 99;
    padding-top: 2em;
  }
}

@-webkit-keyframes moveInBottom {
  0% {
    opacity: 0;
    transform: translateY(3rem);
  }
  80% {
    transform: translateY(-0.5rem);
  }
  100% {
    opacity: 1;
    transform: translate(0);
  }
}

@keyframes moveInBottom {
  0% {
    opacity: 0;
    transform: translateY(3rem);
  }
  80% {
    transform: translateY(-0.5rem);
  }
  100% {
    opacity: 1;
    transform: translate(0);
  }
}

@-webkit-keyframes moveInRight {
  0% {
    opacity: 0;
    transform: translateX(5rem);
  }
  80% {
    transform: translateX(-0.3rem);
  }
  100% {
    opacity: 1;
    transform: translate(0);
  }
}

@keyframes moveInRight {
  0% {
    opacity: 0;
    transform: translateX(5rem);
  }
  80% {
    transform: translateX(-0.3rem);
  }
  100% {
    opacity: 1;
    transform: translate(0);
  }
}

.main-page {
  min-height: 100%;
}

header {
  background: transparent;
  padding: 0.3em;
  text-align: center;
  margin-bottom: 1em;
}

.container-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-height: 100px;
}

.header-title {
  order: 2;
}

.header-logo {
  max-width: 70px;
  opacity: 0.8;
  margin-bottom: 0.3em;
}

@media (min-width: 450px) {
  .container-header {
    flex-direction: row;
    justify-content: center;
  }
  .header-logo {
    margin-right: 0.5em;
  }
}

@media (min-width: 650px) {
  header {
    max-height: 100px;
  }
}

/* Global Styles */

* {
  box-sizing: border-box;
}

html {
  background-image: linear-gradient(
      to bottom,
      rgba(211, 215, 238, 0.52),
      rgba(117, 19, 93, 0.73)
    ),
    url(/tic-tac-toe-React/static/media/background.af300c21.jpg);

  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  font-size: 75%;
}

body {
  height: 100vh;
  margin: 0;
  font-family: "Lora", serif;
  font-size: 1.4rem;
  font-weight: 300;
}

img {
  max-width: 100%;
  display: block;
}

h2,
h3 {
  font-family: "Lora", serif;
  font-weight: 700;
  font-size: 2rem;
  margin: 0;
}

h1 {
  font-family: "Special Elite", cursive;
  font-size: 2.8rem;
  margin: 0;
}

input[type="button"] {
  font-size: 1.3rem;
  font-weight: 700;
  font-family: "Lora", serif;
  background-color: #4caf4fc9;
  border: none;
  color: white;
  padding: 0.3em 0.8em;
  text-decoration: none;
  margin: 0.3em;
  cursor: pointer;
}

input[type="button"]:hover,
input[type="button"]:focus {
  text-decoration: underline;
}

.container {
  width: 95%;
  max-width: 900px;
  margin: 0 auto;
}

